@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300|Padauk|Quicksand);
.header {
    font-family: 'Roboto Slab';
    font-size: 60px;
}

body {
    height: 100%;
    background-color: rgb(196, 206, 196);
}


.status:hover, .wet:hover {
    cursor: pointer;
}

.fas {
    width: 36px;
    font-size: 36px;
}

.plant {
    position: fixed;
    top: 50%;
    left: 0; 
    -webkit-transform: translate(-50%, -50%); 
            transform: translate(-50%, -50%);
}

ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

ul li {
    position: absolute;
    right: 0;
    width: 90px;
    height: 40px;
    background-color: rgba(0, 0, 0, 0.8);
    color: rgb(143, 143, 143);
    line-height: 40px;
    text-align: center;
    font-size: 10px;
}

ul li ul li:hover {
    background-color: rgb(121, 58, 121);
    cursor: pointer;
}

ul li ul li {
    position: relative;
    border-radius: 0;
    display: none;
}

ul li:hover ul li {
    display: block;
}

ul li ul li .dot {
    height: 10px;
    width: 10px;
    border-radius: 50%;
    display: inline-block;
    margin-left: 4px;
}

ul li ul li .dot:hover {
    cursor: pointer;
}

ul li ul li .color1 {
    background-color:  rgb(196, 206, 196);
}

ul li ul li .color2 {
   background-color: #666
}

ul li ul li .color3 {
    background-color: rgb(144, 144, 41)
}

ul li ul li .color4 {
    background-color: #eec9d2 
}

#logOut, #save {
    display: none;
}

@media only screen and (max-width: 600px) {
    ul {
      display: none;
    }

    #logOut, #save {
        position: absolute;
        height: 15px;
        width: 15px;
        border-radius: 50%;
        display: inline-block;
        margin-right: 14px;
        margin-top: 4px;
        right: 0;
        background-color: rgb(167, 89, 89);
        border: none;
    }

    #logOut:hover {
        cursor: pointer;
    }
  }
.split {
  height: 100%;
  width: 50%;
  position: fixed;
}

.left {
  left: 0;
  background-image: url(/static/media/tallplants.dbba0149.jpg);
  border-top: 27px solid rgb(237, 237, 237);
  border-bottom: 27px solid rgb(237, 237, 237);
  border-left: 21px solid rgb(237, 237, 237);
}

.right {
  right: 0;
  background-color: white;
  border-top: 27px solid rgb(237, 237, 237);
  border-bottom: 27px solid rgb(237, 237, 237);
  border-right: 21px solid rgb(237, 237, 237);
}

.logo {
  font-size: 30px;
  position: absolute;
  bottom: 200%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
}

input[type=password], input[type=email] {
  display: inline-block;
  border: 0.5px solid rgb(234, 233, 233);
  outline: none;
  width: 150px;
  font-size: 20px;
  margin-bottom: 12px; 
}

#login {
  background-color: rgba(0, 0, 0, 0.8);
  color: rgb(143, 143, 143);
  border: none;
  width: 302px;
  font-size: 20px;
  border-radius: 3px;
}

#signup {
  font-size: 15px;
  position: fixed;
  margin-top: 75%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-transform: uppercase;
}

#signup:hover, #login {
  cursor: pointer;
}

@media only screen and (max-width: 800px) {
  .left {
    display: none;
  }

  .right {
    border-top: 20px solid rgb(237, 237, 237);
    border-bottom: 20px solid rgb(237, 237, 237);
    border-right: 15px solid rgb(237, 237, 237);
    border-left: 15px solid rgb(237, 237, 237);
  }

  .split {
    width: 100%;
  }
}
